@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");

:root {
    --base-color: #111;
    --base-light-color: #fff;
    --base-bg-color: #fffff5;
    --header-bg-color: hsl(170, 90%, 25%);
    --sidebar-bg-color: hsl(60, 40%, 90%);
    --active-dialog-arrow-color: hsl(60, 40%, 80%);
    --incom-msg-bg-color: hsl(60, 100%, 88%);
    --outcom-msg-bg-color: hsl(100, 80%, 90%);
    --extra-light-grey-color: hsl(50, 10%, 95%);
    --light-grey-color: hsl(50, 10%, 75%);
    --time-grey-color: hsl(50, 10%, 60%);
    --semi-grey-color: hsl(50, 10%, 50%);
    --user-data-color: var(--semi-grey-color);
    --main-title-color: var(--header-bg-color);
    --links-hover-color: hsl(170, 60%, 40%);
    --links-active-color: hsl(20, 100%, 45%);
    --main-border-color: hsl(60, 20%, 85%);
    --post-border-color: var(--sidebar-bg-color);
    --likes-color: hsl(60, 40%, 40%);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

textarea,
button,
input,
select {
    font-family: inherit;
    font-size: inherit;
    border: none;
    outline: none;
}

body {
    font-family: "IBM Plex Sans", sans-serif;
    font-size: 15px;
    color: var(--base-color);
}

.container {
    display: grid;
    width: 90%;
    border: 1px solid var(--main-border-color);
    max-width: 1200px;
    min-width: 700px;
    margin: 0 auto 100px;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 80px;
    grid-template-areas:
        "header header"
        "sidebar main-content"
        "footer footer";
}

.hidden {
    display: none;
}

img {
    display: block;
}
